import { IClientContext, IStaticContext, ITelemetrySettings, IUserContext } from '.';
import { IAny, IDictionary } from '../interfaces';
import { ICommerceApiSettings } from './ICommerceApiSettings';
import { IActiveExperiments } from './IExperimentation';
import { IGridSettings } from './IGridSettings';
import { IUrlTokens } from './IUrlTokens';

export interface IRenderingContext {
    requestUrl: string;
    canonicalUrl?: string;
    canonicalDomain?: string;
    flightIds: string[];
    staticContext: IStaticContext;
    clientContext: IClientContext;
    user?: IUserContext;
    userContext: IUserContext;
    locale: string;
    textDirection: string;
    query: IDictionary<string>;
    apiSettings: ICommerceApiSettings;
    gridSettings?: IGridSettings;
    urlTokens: IUrlTokens;
    operationId: string;
    market?: string;
    features: IDictionary<boolean>;
    themeOverride?: {
        url: string;
        disableDefault: boolean;
    };
    sameSiteRequired?: boolean;
    telemetrySettings: ITelemetrySettings;
    siteStylePreset?: IDictionary<IDictionary<IAny>>;
    experiments?: IActiveExperiments;
    telemetryPageName?: string;
}
